.reference{
     padding-top: 11.375rem;
     text-align: center;

    .reference__title{
        font-size: 4rem;
        font-weight: 500;
        margin-bottom: 1.6875rem;
    }
    .reference__text{
        font-size: 1.5rem;
        color: getColor(primary-text-color);
        margin-bottom: 5.125rem;
    }
    
    .reference__images {
        display: grid;
        max-width: 100%;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem; 
        align-items: stretch; 
    }

    .reference__images--left {
        display: grid;
        grid-template-rows: minmax(auto 549px) minmax(auto 404px) ;
        gap: 2rem; 
   
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 3.125rem;
    }
    .image__overlay{
        position: relative;
        &::after{
            position: absolute;
            top: 0;
            left: 0;
            content: 'living room';
            display: flex;
            background: rgba(0, 0, 0, 0.3 );
            z-index: 20;
            width: 100%;
            height: 100%;
            border-radius: 3.125rem;
            align-items: center;
            justify-content: center;
            font-size: 64px;
            font-weight: 600;
            color: getColor(light-text-color);


        }
    }
    .reference__images--right {
    display: grid;
    grid-template-rows: auto;

   
    }
}